<template>
    <div class="login-container">
        <div class="login-logo">
            <img src="../assets/logo.jpg" alt="Logo">
            <h2 class="login-title">Log in</h2>
        </div>
        <form @submit.prevent="submitLogin">
            <div class="form-group">
                <input type="email" id="email" v-model="email" placeholder="E-Mail Address" required>
            </div>
            <div class="form-group">
                <input type="password" id="password" v-model="password" placeholder="Password" required>
            </div>
            <div class="form-options">
                <label class="remember-me">
                    <input type="checkbox" v-model="rememberMe"> Remember Me
                </label>
                <a href="#" class="forgot-password">Forgot Password?</a>
            </div>
            <div class="form-group">
                <button type="submit" class="login-button">Log in</button>
            </div>
            <!-- Error message display -->
            <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
        </form>
    </div>
</template>
  
<script>
import axios from 'axios';

export default {
    name: 'LoginVIew',
    data() {
        return {
            email: '',
            password: '',
            rememberMe: false,
            errorMessage: ''
        };
    },
    methods: {
        async submitLogin() {
            try {
                const response = await axios.post('http://localhost:3000/api/login', {
                    email: this.email,
                    password: this.password,
                    rememberMe: this.rememberMe
                });

                if (response.data.success) {
                    // Redirect to the dashboard or home page on successful login
                    console.log("login successfull");
                    this.$router.push({ name: 'homepage' });
                } else {
                    // Update the errorMessage data property to show the error
                    this.errorMessage = response.data.message;
                }
            } catch (error) {
                this.$router.push({ name: 'homepage' });
                this.errorMessage = 'Failed to log in. Please try again.';
                if (error.response) {
                    // The request was made and the server responded with a status code
                    // that falls out of the range of 2xx
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                    this.errorMessage = error.response.data.message || this.errorMessage;
                } else if (error.request) {
                    // The request was made but no response was received
                    console.log(error.request);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
            }
        }
    }
};
</script>
  
